import { axiosInstance } from '@/services/httpService';
import { defineComponent } from 'vue';
import ThunderstormSvg from '@/assets/svg/ThunderstormSvg.vue';
import DizzleSvg from '@/assets/svg/DizzleSvg.vue';
import RainSvg from '@/assets/svg/RainSvg.vue';
import FogSvg from '@/assets/svg/FogSvg.vue';
import DustSvg from '@/assets/svg/DustSvg.vue';
import SqualSvg from '@/assets/svg/SqualSvg.vue';
import TornadoSvg from '@/assets/svg/TornadoSvg.vue';
import ClearSvg from '@/assets/svg/ClearSvg.vue';
import CloudsSvg from '@/assets/svg/CloudsSvg.vue';
import SnowSvg from '@/assets/svg/SnowSvg.vue';
import { mapMutations } from 'vuex';
export default defineComponent({
    name: 'FavoriteCity',
    components: {
        ThunderstormSvg,
        DizzleSvg,
        RainSvg,
        FogSvg,
        DustSvg,
        SqualSvg,
        TornadoSvg,
        ClearSvg,
        CloudsSvg,
        SnowSvg
    },
    data() {
        return {
            weatherCod: 0,
            city: '',
            cityTemp: 0,
            cityIconId: ''
        };
    },
    props: {
        cityName: {
            type: String,
            required: true
        },
    },
    mounted() {
        this.getCurrentWeather();
    },
    methods: {
        ...mapMutations({
            toggleVisibilityHeader: 'toggleVisibilityHeader'
        }),
        async getCurrentWeather() {
            try {
                const response = await axiosInstance.get(`https://api.openweathermap.org/data/2.5/weather?q=${this.cityName}&appid=83d9c12644f83eb94f3ad2d262c259e2&units=metric&lang=ru`);
                this.city = response.data.name;
                this.cityTemp = response.data.main.temp;
                this.cityIconId = response.data.weather[0].icon;
                this.weatherCod = response.data.weather[0].id;
            }
            catch (e) {
                // console.error(e)
            }
        },
        toWeatherPage(path) {
            this.toggleVisibilityHeader(false);
            this.$router.push(`/weather=${path}`);
        }
    }
});
