import FavoriteCity from '@/components/shared/FavoriteCity/FavoriteCity.vue';
import { defineComponent } from 'vue';
import { mapGetters, mapMutations } from 'vuex';
export default defineComponent({
    components: { FavoriteCity },
    name: 'FavoriteCities',
    data() {
        return {
            favoriteCities: []
        };
    },
    methods: {
        ...mapGetters({
            getFavoriteCities: 'getFavoriteCities',
        }),
        ...mapMutations({
            setFavoriteCities: 'setFavoriteCities'
        })
    },
    mounted() {
        this.setFavoriteCities(localStorage.getItem('favoriteCities')?.split(','));
        this.favoriteCities = this.getFavoriteCities();
    }
});
